h1, h2, h3, h4 {
    margin: 0;
}
.header {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    width: 100%;
    border-bottom: 1px solid gray;
    box-shadow: 0px 0px 1px 0px gray;
    /*opacity: 0;*/
}
.nav {
    margin: 0;
    padding: 0 0rem 0 0rem;
    display: flex;
    flex-direction: row;
    flex: 4 0 2rem;
    align-items: center;
    justify-content: space-evenly ;    
}
.logo {
    flex: 1 0 2rem;
    display: flex;
    min-width: 2rem;
    align-items: center;
    justify-content: center;
}
.logo img {
    cursor: pointer;
    width: auto;
    height: 50px;
}
.navItem {
    cursor: pointer;
    justify-content: center;
    align-items: center;
}
.footer {
    display: flex;
    height: 70px;
    border-top: 1px solid gray;
    box-shadow: 1px 0px 0px 0px gray;
    justify-content: center;
}
.footerText{
    display: flex;
    align-items: center;
}
#regSection, #logSection, #homePage {
    margin: 0;
    box-sizing: border-box;
    min-height: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#homePage {

}
.detailPage{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.buttonContain{
    position: fixed;
    display: flex;
    justify-content: center;
    width: 90%;
    max-width: 900px;
    gap: 2rem;
    margin-top: 2rem;
    padding: 0 0.2rem;
    justify-content: space-between;
    bottom: 50%;
    z-index: 1;
    
}
.button{
    width: 3rem;
    height: 3rem;
    z-index: 3 !important;
    background-color: aquamarine;
}
.pageSetup{
    position: relative;
    max-width: 1000px;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    z-index: 2;
}
.group {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.container {
    position: relative;
    border: solid gray 1px;
    border-radius: 3px;
    width: 90%;
    max-width: 600px;
    text-align: center;
    margin: 2rem 0;
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    box-shadow: 1px 1px 1px 0px gray;
}
.genresList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    padding: 1rem;
}
.title {
    display: flex;
    justify-content: center;
    padding: 1rem;
}
.genresItem {
    cursor: pointer;
}
.filmList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
}
.filmLink {
    width: 100%;
    display: flex;
    justify-content: center;
}
.filmItem {
    width: 80%;
    padding: 0.5rem 1rem;
    min-height: 10rem;
    display: flex;
    cursor: pointer;
    border: 1px rgb(41, 29, 29) solid;
    align-items: center;
    gap: 0.5rem;
    border-radius: 3px;
}
.left {
    display: flex;
    align-items: center;
    flex: 1 1 10rem;
}
.right {
}
.focus {
    opacity: 1;
    color: green;
    background-color:aliceblue;
    transition: all 0.5s linear 0.2s;
}
.visible {
    opacity: 1;
    transition: all 0.5s linear 0.2s;
}
.invisible {
    opacity: 0;
    transition: all 0.5s linear 0.2s;
}
.main {
    min-height: 80svh;
}
@media screen and (max-width: 500px) {
    .pageSetup{
        max-width: 500px;
        margin: auto;
        border: 1px rebeccapurple solid;
    }}
    h1 {
        font-size: 1.5rem;
    }